.singlephotogallery-container {
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
}
.singlephotogallery-container > .left,
.singlephotogallery-container > .right {
  position: absolute;
  width: 50%;
  height: 100%;
  top: 0;
}
.singlephotogallery-container > .left {
  left: 0;
  cursor: w-resize;
}
.singlephotogallery-container > .right {
  right: 0;
  cursor: e-resize;
}
.singlephotogallery-container > img {
  /* the cover of milkfat is fitting strangely */
  -o-object-fit: contain;
     object-fit: contain;
  width: 100%;
}
.mobileOnly {
  display: none;
}

.singlephotogallery-container > img {
  height: 100%;
  margin-right: 12px;
}

.mobileOnly {
  display: none;
}
@media only screen and (max-width: 768px) {
  .singlephotogallery-container {
    display: none;
  }

  .mobileOnly {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .mobileOnly > div > img {
    width: 100%;
  }
}



