@import url('https://fonts.googleapis.com/css?family=Karla');
@import url('https://fonts.googleapis.com/css?family=Source+Code+Pro');
@import url(http://fonts.googleapis.com/earlyaccess/notosanssc.css);

body,
html,
#root {
  width: 100%;
  margin: 0;
  font-family: 'Karla', sans-serif;
}

::-webkit-scrollbar {
  display: none;
}
::-moz-selection {
  background: white;
}
::selection {
  background: white;
}

* {
  -webkit-overflow-scrolling: touch;
}

a {
  font-weight: bold;
  text-decoration: none;
  color: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

.brutalism {
  width: 300vw;
}

.container {
  width: 100%;
}

.kevin,
.lee,
.mngyuan {
  cursor: default;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: fixed;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  font-size: 15vw;
  color: #f1f0f0;
}
.kevin,
.lee {
  font-family: 'Karla', sans-serif;
  font-weight: bold;
}
.kevin {
  left: calc(-15vw / 3);
}
.mngyuan {
  font-family: 'Noto Sans SC', sans-serif;
  font-weight: bolder;
  width: 100%;
  text-align: center;
  margin-left: calc(15vw / 3);
}
.lee {
  right: calc(-15vw / 10);
}

.page {
  min-height: 100vh;
  position: relative;
}
.page {
  height: 100%;
}

.page.black {
  background: black;
  overflow: hidden;
}
.page.black video {
  /* from stack overflow
   * https://stackoverflow.com/questions/10797632/simulate-background-sizecover-on-video-or-img
   */
  height: 100%;
  width: 177.77777778vh; /* 100 * 16 / 9 */
  min-width: 100%;
  min-height: 56.25vw; /* 100 * 9 / 16 */

  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}
.page.black.film {
  background-image: url(./photos/filmbg.jpg);
  background-size: 100% auto;
  background-repeat: repeat-y;
  background-position: center center;
}

.theOne {
  position: fixed;
  bottom: 10vh;
  left: 10vw;
  font-family: 'Karla', sans-serif;
  font-size: 15px;
  line-height: 21px;
  width: 305px;
  max-height: 540px;
  height: 70%;
  padding: 20px 40px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.theOne.black {
  background: none;
  color: white;
}

.theOneInner {
  position: absolute;
  bottom: 0;
  padding: inherit;
  left: 0;
}

.theOneDecor {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: inherit;
  height: 80px;
  border-top: 12px solid rgba(255, 0, 0, 0.5);
  border-left: 12px solid rgba(255, 0, 0, 0.5);
  border-right: 12px solid rgba(69, 91, 159, 0.5);
}

.line {
  width: 50%;
  background-color: black;
  height: calc(21px * 0.2);
  margin-top: calc(21px * 0.4);
  margin-bottom: calc(21px * 0.4);
}
.mePhoto {
  position: absolute;
  bottom: 20vh;
  left: 24vw;
  width: 64vw;
  height: 64vh;
}
.mePhoto > img {
  height: 100%;
}

.photowrapper {
  height: 80vh;
  position: absolute;
  top: 15vh;
  bottom: 5vh;
  width: 100%;
  overflow: scroll;
}
.photowrapperInner {
  height: 100%;
  padding-left: 15vw;
  padding-right: 15vw;
  white-space: nowrap;
  overflow: auto;
  display: -ms-flexbox;
  display: flex;
}
.photowrapper.vertical {
  top: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
}
.photowrapperInner {
  padding-left: 5vw;
  padding-right: 5vw;
}
.photowrapper.vertical .photowrapperInner {
  -ms-flex-direction: column;
      flex-direction: column;
}
.photowrapper.vertical .photowrapperInner > div {
  margin-bottom: 76px;
}
.photowrapper.vertical .photowrapperInner > div:nth-last-child(1) {
  margin: 0;
}
.photowrapper.vertical .photowrapperInner > div > img {
  width: 100%;
}

.photowrapperInner > img {
  height: 100%;
  margin-right: 12px;
}

.preamble {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: end;
      align-items: flex-end;
  font-family: 'Source Code Pro', monospace;
  font-size: 10px;
  padding-left: 24px;
  padding-right: 96px;
}

.story .photowrapperInner > img {
  margin-right: 0;
}

.textColumn {
  font-size: 15px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 48px;
  white-space: pre;
}

.textColumn.middle {
  -ms-flex-pack: center;
      justify-content: center;
}

.textColumn.bottom {
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.videowrapper > iframe {
  height: 360px;
  float: right;
}

.topbar {
  position: fixed;
  top: 0;
  font-family: 'Source Code Pro', monospace;
  font-size: 12px;
  text-align: right;
  width: 100%;
  padding: 12px 24px;
  padding-top: 48px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.topbar .nameplate {
  font-size: 24px;
  font-family: 'Karla', sans-serif;
  font-weight: normal;
}
.topbar.black .nameplate {
  color: white;
}

.navMenu {
  position: absolute;
  right: 0;
  width: 400px;
  display: none;
}

nav .section {
  display: inline-block;
  position: relative;
  font-weight: normal;
  color: rgba(0, 0, 0, 0.5);
}
nav .section:hover .navMenu {
  display: block;
}
.black nav a {
  color: white;
}
nav a.active,
nav a:hover {
  font-weight: bold;
  color: red;
}

.blogEntry {
  height: 100vh;
  background-color: #d3d8dc;
  font-family: 'Source Code Pro', sans-serif;
}
.blogEntryInner {
  height: 100%;
  max-height: 930px;
  position: relative;
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  top: 50%;
}
.blogEntryInner p {
  font-size: 8pt;
}
.blogEntryInner .photo {
  position: relative;
  display: inline-block;
  height: 98%;
  top: 1%;
  margin-right: 48px;
}
.blogEntryInner .photo > img {
  height: 100%;
  width: auto;
  z-index: 1;
}
.blogEntryInner .photo.inset {
  padding: 8% 16%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  /* for some reason adding padding here moves the entire div down; correct */
  top: -16%;
}
.blogBlurb {
  position: absolute;
  left: 5vw;
  top: 15vh;
  width: 15vw;
}
.blogBlurb > img {
  height: 500px;
}
.blogBlurb > h1 {
  text-transform: uppercase;
  font-weight: lighter;
  letter-spacing: 2em;
  margin-bottom: 8px;
}
.blogBlurb > .line {
  height: 21px;
  margin: 0;
}
.columnsText {
  position: absolute;
  /*columns: 2 auto;*/
  /*column-fill: auto;*/
  width: 200px;
  color: white;
  white-space: initial;
  z-index: 2;
}
.flavorText {
  position: absolute;
  text-transform: uppercase;
  font-family: 'Karla', sans-serif;
  color: white;
  letter-spacing: 2em;
  z-index: 2;
}

.contactSheet {
  color: #f7f4ec;
  width: 100%;
}
.contactSheetHeader {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: start;
      align-items: flex-start;
  overflow: scroll;
}
.contactSheetHeader > .headerInfo {
  white-space: pre-wrap;
  padding: 48px;
}
.contactSheetHeader > .headerInfo.secondary {
  color: grey;
}
.contactSheetHeader > .headerInfo > .small {
  font-size: small;
}
.contactSheet > .frames {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  width: calc(100% - 60px);
  margin: 0 auto;
  margin-bottom: 100px;
}
.frames > .frame {
  width: calc(10% - 4px);
  position: relative;
  margin-right: 4px;
}
.frames > .frame > img {
  width: 100%;
}
.frames > .frame > .fileName {
  position: absolute;
  bottom: 4px;
  left: 4px;
  font-weight: bold;
  font-size: 10px;
  color: rgba(255, 255, 33, 0.5);
}
.contactSheet > .downloadLink {
  text-transform: uppercase;
  color: grey;
  display: block;
  margin: 48px;
}

@media only screen and (max-width: 768px) {
  .photowrapper {
    height: 100vh;
    top: 0;
  }

  .photowrapperInner {
    padding-top: 15vh;
    display: block;
  }

  .photowrapperInner > img {
    display: block;
    width: 100%;
    height: auto;
    margin: 16px 0;
  }
  .topbar {
    padding: 24px;
    display: block;
    font-size: 12px;
    width: auto;
    right: 0;
  }
  .topbar.black {
    color: red;
  }
  .topbar .nameplate {
    font-size: 24px;
  }
  .topbar.black .nameplate {
    color: black;
  }

  .nameplate,
  .black .nameplate {
    background: white;
  }

  nav .section {
    display: block;
  }
  nav .sectionLink {
    background: black;
    color: white;
  }
  .navMenu {
    display: block;
    position: initial;
    width: auto;
  }
  .navMenu a {
    background: white;
  }
  nav .divider {
    display: none;
  }

  .black nav .sectionLink {
    background: white;
    color: black;
  }
  .black .navMenu a {
    background: black;
    color: white;
  }
  .black nav a.active,
  .black nav a:hover {
    color: red;
  }

  .desktopOnly {
    display: none !important;
  }

  /*.story .photowrapperInner > img {*/
  /*transform: rotate(90deg);*/
  /*margin: 0;*/
  /*}*/
  .story .photowrapperInner {
    display: -ms-flexbox;
    display: flex;
    padding-top: 0;
  }
  .story .photowrapperInner > img {
    width: auto;
    height: 100%;
    margin: 0;
  }

  .contactSheetHeader {
    padding: 30px;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }
  .contactSheetHeader > .headerInfo {
    padding: 8px 0;
    width: 50%;
  }
  .contactSheetHeader > .headerInfo:nth-child(even) {
    text-align: right;
  }
  .frames > .frame {
    width: calc(50% - 4px);
    position: relative;
    margin-right: 4px;
  }

  .photowrapperInner.single {
    display: none;
  }
}



